<template>
  <div class="WebSerialApi">
    <el-button @click="get_one"></el-button>
    <el-button @click="get_open">open</el-button>
    <el-button @click="get_close">close</el-button>
    <el-button @click="get_new"></el-button>
  </div>
</template>

<script>
export default {
  name: 'WebSerialApi',
  components: {
  },
  data () {
    return {
      ws: ''
    }
  },
  mounted () {
    this.webSocket()
  },
  methods: {
    // websocket
    webSocket () {
      if ('WebSocket' in window) {
        let that = this
        that.ws = new WebSocket('ws://127.0.0.1:8003')
        console.log(that.ws)
        that.ws.onopen = function () {
          console.log('open')
          // var message = JSON.stringify(this.data)
          that.ws.send('')
        }
        that.ws.onmessage = function (e) {
          console.log(e)
          if (e.data === '') {
            that.$message({
              type: 'success',
              message: '!'
            })
          } else if (e.data === '') {
            that.$message({
              type: 'success',
              message: '!'
            })
          } else if (e.data === '') {
            that.$message({
              type: 'error',
              message: '!'
            })
          } else if (e.data === '') {
            that.$message({
              type: 'success',
              message: '!'
            })
          } else if (e.data === '') {
            that.$message({
              type: 'success',
              message: '!'
            })
          } else if (e.data === '') {
            that.$message({
              type: 'success',
              message: '!'
            })
          } else if (e.data === '') {
            that.$message({
              type: 'success',
              message: '!'
            })
          }
        }
        that.ws.onerror = function (e) {
          that.$message({
            type: 'error',
            message: ''
          })
        }
        that.ws.onclose = function (e) {
          console.log(e)
          that.get_new()
        }
      } else {
        console.log('WebSocket')
      }
    },
    get_new () {
      this.webSocket()
    },
    get_one () {
      let that = this
      that.timer = setInterval(() => {
        that.ws.send('')
      }, 1000 * 5)
    },
    get_open () {
      let that = this
      that.ws.send('')
    },
    get_close () {
      let that = this
      that.ws.send('')
    }
  }
}
</script>

